'use client'

import { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Button } from '@/components/ui/button'
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
  FormDescription,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Checkbox } from '@/components/ui/checkbox'
import { ImageUpload } from '@/components/ui/image-upload'
import { RENTAL_PLANS, IndividualCabinFields, FacilityFormProps } from '../types'
import { MultiSelect } from '@/components/ui/multi-select'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'

const formSchema = z.object({
  name: z.string().min(1, 'Name is required'),
  description: z.string().min(1, 'Description is required'),
  images: z.array(z.string()).min(1, 'At least one image is required'),
  videoLink: z.string().optional(),
  selectedRentalPlans: z.array(z.enum(RENTAL_PLANS)).min(1, 'At least one rental plan is required'),
  totalCabins: z.number().min(1, 'Must have at least 1 cabin'),
  availableCabins: z.number().min(0),
  rentPerYear: z.number().optional(),
  rentPerMonth: z.number().optional(),
  rentPerWeek: z.number().optional(),
  dayPassRent: z.number().optional(),
  location: z.string().min(1, 'Location is required'),
  area: z.string().min(1, 'Area is required'),
  capacity: z.string().min(1, 'Capacity is required'),
  amenities: z.array(z.string()),
  price: z.string().min(1, 'Price is required'),
  priceUnit: z.enum(['per-hour', 'per-day', 'per-month', 'per-year']),
  availability: z.enum(['available', 'occupied', 'maintenance']),
})

type FormValues = z.infer<typeof formSchema>

const amenitiesOptions = [
  { value: 'wifi', label: 'WiFi' },
  { value: 'air-conditioning', label: 'Air Conditioning' },
  { value: 'heating', label: 'Heating' },
  { value: 'projector', label: 'Projector' },
  { value: 'whiteboard', label: 'Whiteboard' },
  { value: 'printer', label: 'Printer' },
  { value: 'scanner', label: 'Scanner' },
  { value: 'parking', label: 'Parking' },
  { value: 'security', label: '24/7 Security' },
  { value: 'cleaning', label: 'Cleaning Service' },
  { value: 'reception', label: 'Reception' },
  { value: 'kitchen', label: 'Kitchen' },
  { value: 'lounge', label: 'Lounge Area' },
  { value: 'meeting-room', label: 'Meeting Room Access' },
  { value: 'storage', label: 'Storage Space' },
]

export function IndividualCabinForm({
  onSubmit,
  onChange,
  initialData,
  readOnly,
}: FacilityFormProps) {
  const [images, setImages] = useState<string[]>([])

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: initialData || {
      name: '',
      description: '',
      images: [],
      videoLink: '',
      selectedRentalPlans: [],
      totalCabins: 0,
      availableCabins: 0,
      location: '',
      area: '',
      capacity: '',
      amenities: [],
      price: '',
      priceUnit: 'per-month',
      availability: 'available',
    },
  })

  const selectedRentalPlans = form.watch('selectedRentalPlans') || []

  useEffect(() => {
    if (onChange) {
      const subscription = form.watch(() => onChange())
      return () => subscription.unsubscribe()
    }
  }, [form, onChange])

  const handleImageUpload = (newImages: string[]) => {
    setImages(newImages)
    form.setValue('images', newImages)
    onChange?.()
  }

  const handleFormSubmit = async (values: FormValues) => {
    try {
      console.log('Form submission started - Individual Cabin Form')
      console.log('Form values:', values)
      
      if (images.length === 0) {
        console.log('Validation failed: No images uploaded')
        form.setError('root', {
          type: 'manual',
          message: 'Please upload at least one image'
        })
        return
      }

      if (!values.selectedRentalPlans.length) {
        console.log('Validation failed: No rental plans selected')
        form.setError('root', {
          type: 'manual',
          message: 'Please select at least one rental plan'
        })
        return
      }

      // Check if rent values are provided for selected rental plans
      const rentValidation = values.selectedRentalPlans.every(plan => {
        switch (plan) {
          case 'Annual':
            return !!values.rentPerYear
          case 'Monthly':
            return !!values.rentPerMonth
          case 'Weekly':
            return !!values.rentPerWeek
          case 'One Day (24 Hours)':
            return !!values.dayPassRent
          default:
            return false
        }
      })

      if (!rentValidation) {
        console.log('Validation failed: Missing rent values for selected plans')
        form.setError('root', {
          type: 'manual',
          message: 'Please provide rent values for all selected rental plans'
        })
        return
      }

      // Validate cabin counts
      if (values.totalCabins <= 0) {
        console.log('Validation failed: Invalid total cabins')
        form.setError('totalCabins', {
          type: 'manual',
          message: 'Total cabins must be greater than 0'
        })
        return
      }

      if (values.availableCabins > values.totalCabins) {
        console.log('Validation failed: Available cabins exceed total cabins')
        form.setError('availableCabins', {
          type: 'manual',
          message: 'Available cabins cannot exceed total cabins'
        })
        return
      }

      // Prepare final submission data
      const submissionData = {
        type: 'individual-cabin' as const,
        name: values.name,
        description: values.description,
        images,
        videoLink: values.videoLink || '',
        totalCabins: Number(values.totalCabins),
        availableCabins: Number(values.availableCabins),
        rentalPlans: values.selectedRentalPlans.map(plan => {
          let price = 0;
          switch (plan) {
            case 'Annual':
              price = values.rentPerYear || 0;
              break;
            case 'Monthly':
              price = values.rentPerMonth || 0;
              break;
            case 'Weekly':
              price = values.rentPerWeek || 0;
              break;
            case 'One Day (24 Hours)':
              price = values.dayPassRent || 0;
              break;
          }
          return {
            name: plan,
            price,
            duration: plan
          };
        })
      }

      console.log('Submitting data:', submissionData)
      await onSubmit(submissionData)
      console.log('Form submission successful')
      
      // Reset form state
      form.reset()
      setImages([])
      onChange?.()
    } catch (error) {
      console.error('Form submission error:', error)
      form.setError('root', { 
        type: 'manual',
        message: 'Failed to submit form. Please try again.'
      })
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleFormSubmit)} className="space-y-8">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Facility Name *</FormLabel>
                <FormControl>
                  <Input {...field} disabled={readOnly} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="description"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Description *</FormLabel>
                <FormControl>
                  <Textarea {...field} disabled={readOnly} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="totalCabins"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Total No. of individual cabins available *</FormLabel>
                <FormControl>
                  <Input 
                    type="number" 
                    min={0}
                    {...field} 
                    onChange={e => {
                      const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                      field.onChange(value)
                    }} 
                    value={field.value || ''}
                    disabled={readOnly}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="availableCabins"
            render={({ field }) => (
              <FormItem>
                <FormLabel>No. of Cabins currently available for occupancy *</FormLabel>
                <FormControl>
                  <Input 
                    type="number" 
                    min={0}
                    {...field} 
                    onChange={e => {
                      const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                      field.onChange(value)
                    }} 
                    value={field.value || ''}
                    disabled={readOnly}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <div className="space-y-4">
          <FormLabel>Kindly upload the photos of the Individual Cabins *</FormLabel>
          <FormControl>
            <ImageUpload
              value={images}
              disabled={readOnly}
              onChange={handleImageUpload}
              onRemove={(url) => {
                const newImages = images.filter((image) => image !== url)
                handleImageUpload(newImages)
              }}
            />
          </FormControl>
          {form.formState.errors.root && (
            <p className="text-sm text-destructive">{form.formState.errors.root.message}</p>
          )}
        </div>

        <FormField
          control={form.control}
          name="videoLink"
          render={({ field }) => (
            <FormItem>
              <FormLabel>If available, kindly attach Video Link for your Facility</FormLabel>
              <FormControl>
                <Input type="url" placeholder="Google Drive / Youtube link" {...field} disabled={readOnly} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="selectedRentalPlans"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Rental Subscription Plan(s) Available *</FormLabel>
              <FormControl>
                <div className="space-y-2">
                  {RENTAL_PLANS.map((plan) => (
                    <div key={plan} className="flex items-center space-x-2">
                      <Checkbox
                        checked={field.value?.includes(plan)}
                        onCheckedChange={(checked) => {
                          const updatedPlans = checked
                            ? [...(field.value || []), plan]
                            : field.value?.filter((p) => p !== plan) || []
                          field.onChange(updatedPlans)
                          
                          if (!checked) {
                            switch (plan) {
                              case 'Annual':
                                form.setValue('rentPerYear', undefined)
                                break
                              case 'Monthly':
                                form.setValue('rentPerMonth', undefined)
                                break
                              case 'Weekly':
                                form.setValue('rentPerWeek', undefined)
                                break
                              case 'One Day (24 Hours)':
                                form.setValue('dayPassRent', undefined)
                                break
                            }
                          }
                        }}
                        disabled={readOnly}
                      />
                      <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                        {plan}
                      </label>
                    </div>
                  ))}
                </div>
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {selectedRentalPlans.includes('Annual') && (
            <FormField
              control={form.control}
              name="rentPerYear"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rent per Year (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                      disabled={readOnly}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
          {selectedRentalPlans.includes('Monthly') && (
            <FormField
              control={form.control}
              name="rentPerMonth"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rent per Month (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                      disabled={readOnly}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
          {selectedRentalPlans.includes('Weekly') && (
            <FormField
              control={form.control}
              name="rentPerWeek"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rent per Week (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                      disabled={readOnly}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
          {selectedRentalPlans.includes('One Day (24 Hours)') && (
            <FormField
              control={form.control}
              name="dayPassRent"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Day Pass Rent (1 Day) (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                      disabled={readOnly}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="location"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Location *</FormLabel>
                <FormControl>
                  <Input {...field} disabled={readOnly} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="area"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Area (sq ft) *</FormLabel>
                <FormControl>
                  <Input {...field} type="number" disabled={readOnly} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="capacity"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Capacity (persons) *</FormLabel>
                <FormControl>
                  <Input {...field} type="number" disabled={readOnly} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="amenities"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Amenities *</FormLabel>
                <FormControl>
                  <MultiSelect
                    selected={field.value || []}
                    options={amenitiesOptions}
                    onChange={field.onChange}
                    disabled={readOnly}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="price"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Price *</FormLabel>
                <FormControl>
                  <Input {...field} type="number" disabled={readOnly} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="priceUnit"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Price Unit *</FormLabel>
                <Select
                  onValueChange={field.onChange}
                  defaultValue={field.value}
                  disabled={readOnly}
                >
                  <FormControl>
                    <SelectTrigger>
                      <SelectValue placeholder="Select a price unit" />
                    </SelectTrigger>
                  </FormControl>
                  <SelectContent>
                    <SelectItem value="per-hour">Per Hour</SelectItem>
                    <SelectItem value="per-day">Per Day</SelectItem>
                    <SelectItem value="per-month">Per Month</SelectItem>
                    <SelectItem value="per-year">Per Year</SelectItem>
                  </SelectContent>
                </Select>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <FormField
          control={form.control}
          name="availability"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Availability *</FormLabel>
              <Select
                onValueChange={field.onChange}
                defaultValue={field.value}
                disabled={readOnly}
              >
                <FormControl>
                  <SelectTrigger>
                    <SelectValue placeholder="Select availability status" />
                  </SelectTrigger>
                </FormControl>
                <SelectContent>
                  <SelectItem value="available">Available</SelectItem>
                  <SelectItem value="occupied">Occupied</SelectItem>
                  <SelectItem value="maintenance">Under Maintenance</SelectItem>
                </SelectContent>
              </Select>
              <FormMessage />
            </FormItem>
          )}
        />

        <div className="flex justify-end">
          <Button 
            type="submit" 
            className="w-full md:w-auto bg-primary hover:bg-primary/90"
            disabled={form.formState.isSubmitting || readOnly}
          >
            {form.formState.isSubmitting ? (
              <>
                <span className="mr-2">Submitting...</span>
                <span className="animate-spin">⏳</span>
              </>
            ) : (
              'Submit for Approval'
            )}
          </Button>
        </div>
      </form>
    </Form>
  )
} 